  display: flex;
}

.fab {
  font-size: 15px;
  width: 30px;
  height: 30px;
  border-radius: 5px;
  color: #fff !important;
  padding: 7px 0;
  text-align: center;
}
.apsc-count {
  margin-bottom: -9px;
  flex-wrap: wrap;
  font-size: 14px;
  display: block;
  font-weight: 700;
  margin-left: 12px;
}
.apsc-media-type {
  font-size: 11px;

  margin-left: 12px;
  margin-top: 10px;
  font-weight: 500;
}
.div {
  height: 40px;
}
