* {
  box-sizing: border-box;
}

h1.title {
  font-weight: bold;
  margin: 0;
}

p.desc {
  font-size: 14px;
  font-weight: 100;
  line-height: 20px;
  letter-spacing: .5px;
  margin: 20px 0 30px;
}

a.fpw {
  color: #333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

.container-l {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
}

.form-container-l form {
  background: #fff;
  display: flex;
  flex-direction: column;
  padding:  0 50px;
  height: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.social-container-l {
  margin: 20px 0;
}

.social-container-l a {
  border: 1px solid #ddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}

.form-container-l input {
  background: #eee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
}

button.login {
  border-radius: 20px;
  border: 1px solid rgb(6, 30, 126);
  background: rgb(13, 36, 129);
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  transition: transform 80ms ease-in;
}

button.login:active {
  transform: scale(.95);
}

button.login:focus {
  outline: none;
}

button.login.ghost {
  background: transparent;
  border-color: #fff;
}

.form-container-l {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all .6s ease-in-out;
}

.sign-in-container-l {
  left: 0;
  width: 50%;
  z-index: 2;
}

.sign-up-container-l {
  left: 0;
  width: 50%;
  z-index: 1;
  opacity: 0;
}

.overlay-container-l {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform .6s ease-in-out;
  z-index: 100;
}

.overlay-l {
  background: rgb(29, 98, 231);
  background: linear-gradient(to right, rgb(13, 41, 155), rgb(29, 98, 231)) no-repeat 0 0 / cover;
  color: #fff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateY(0);
  transition: transform .6s ease-in-out;
}

.overlay-panel-l {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 40px;
  height: 100%;
  width: 50%;
  text-align: center;
  transform: translateY(0);
  transition: transform .6s ease-in-out;
}

.overlay-right-l {
  right: 0;
  transform: translateY(0);
}

.overlay-left-l {
  transform: translateY(-20%);
}

/* Move signin to right */
.container-l.right-panel-active .sign-in-container-l {
  transform: translateY(100%);
}

/* Move overlay to left */
.container-l.right-panel-active .overlay-container-l {
  transform: translateX(-100%);
}

/* Bring signup over signin */
.container-l.right-panel-active .sign-up-container-l {
  transform: translateX(100%);
  opacity: 1;
  z-index: 5;
}

/* Move overlay back to right */
.container-l.right-panel-active .overlay-l {
  transform: translateX(50%);
}

/* Bring back the text to center */
.container-l.right-panel-active .overlay-left-l {
  transform: translateY(0);
}

/* Same effect for right */
.container-l.right-panel-active .overlay-right-l {
  transform: translateY(20%);
}

.footer-l {
	margin-top: 25px;
	text-align: center;
}

.icons-l {
	display: flex;
	width: 30px;
	height: 30px;
	letter-spacing: 15px;
	align-items: center;
}

.input-error {
  text-align: left;
  width: 100%;
  color: red;
}
